.ChristmasTreeWall {
  margin-top: 10px;
  width: 45vw;
  height: 90vh;
  display: flex;
  align-items: end;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.ChristmasTreeWall img {
  width: 70%;
  height: 95%;
}
.ChristmasTreeWall .toyOfTheTree{
  position: absolute;
  width: 50px;
  height: 50px;
  cursor: grab;
}
.canvas{
  position: absolute;
  width: 35%;
  height: 87%;
  top: 11%;
  background-color: rgba(153, 40, 40, 0.42);
}